<template>
  <div class="about">
    <h1>Testing socket server</h1>
    <!-- <button @click="connect">CONNECT</button> -->
    <input v-on:keyup.enter="send" type="text" v-model="text" />
    <button v-on:keyup.enter="send" @click="send">SEND</button>
    <div class="msg" v-if="!isConnected2 && !getError2">Подключение..</div>
    <div class="msg" v-else-if="!isConnected2 && getError2">Ошибка подключения</div>
    <div v-else class="msg">
      <p>Подключение установлено</p>
      <div style="min-height: 146px; margin-top: 40px; background: rgba(60, 90, 150, .5); border: 2px solid grey; border-radius: 10px; width: 500px; margin: 0 auto">
        <h5 style="color: #fff">Ответ эхо-сервера: </h5>
        <p style="color: #fff"><span style="font-size: 16px">{{socketMsg2}}</span></p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'Sockets',
  data() {
    return {
      text: '',
      socketAnswer: ''
    }
  },
  computed: {
    ...mapGetters({
      isConnected2: 'isConnected2',
      socketMsg2: 'getSocketMsg2',
      getError2: 'getError2'
    })
  },
  methods: {
    ...mapActions({
      sendMessage2: 'sendMessage2'
    }),
    send() {
      this.sendMessage2(this.text)
      this.text = '';
    }
  }
}
</script>

<style lang="scss">
.msg {
  font-size: 24px;
  font-weight: 700;
  margin-top: 100px;
}
</style>