<template>
  <div class="about">
    <h1>Testing fake socket echo server</h1>
    <!-- <button @click="connect">CONNECT</button> -->
    <input v-on:keyup.enter="send" type="text" v-model="text" />
    <button v-on:keyup.enter="send" @click="send">SEND</button>
    <div class="msg" v-if="!isConnected">Ожидаем подключения...</div>
    <div class="msg" v-else-if="!test">Ожидаем тест... (введите 'test')</div>
    <div class="msg" v-else-if="!settings">Ожидаем настройки... (введите 'settings')</div>
    <div class="msg" v-else>
      <p>Подключение установлено, настройки получены, готов к работе</p>
      <div style="min-height: 146px; margin-top: 40px; background: rgba(60, 90, 150, .5); border: 2px solid grey; border-radius: 10px; width: 500px; margin: 0 auto">
        <h5 style="color: #fff">Ответ эхо-сервера: </h5>
        <p style="color: #fff"><span style="font-size: 16px">{{socketMsg}}</span></p>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
  name: 'Sockets',
  data() {
    return {
      text: '',
      socketAnswer: ''
    }
  },
  computed: {
    ...mapGetters({
      isConnected: 'isConnected',
      test: 'isTestRecieved',
      settings: 'isSettingsRecieved',
      socketMsg: 'getSocketMsg'
    })
  },
  methods: {
    ...mapActions({
      sendMessage: 'sendMessage'
    }),
    send() {
      let msg;
      if (this.text == 'test' || this.text == 'settings') {
        msg = {
          type: this.text,
          first: 'первый',
          second: 'второй'
        }
        msg = JSON.stringify(msg)
      } else {
        msg = this.text;
      }
      this.sendMessage(msg)
      this.text = '';
    }
  }
}
</script>

<style lang="scss">
.msg {
  font-size: 24px;
  font-weight: 700;
  margin-top: 100px;
}
</style>